<script>
    import {getorder, getaddress, updataorder} from "../../api/orders";
    import {SUCCESS} from "../../config/base";
    import {IMGPATH} from "../../config/base";
    import PHeader from '../../components/p-header/p-header';
    export default {
        name: "Orders",
        components:{
          PHeader
        },
        data() {
            return {
                message: '',
                address: [],
                resers: '',
                orders: [],
                ordernum: '',
                imgpath: IMGPATH,
                remark:''
            }
        },
        methods: {
            getoederdata(oid) {
                getorder('/api/orders/' + oid).then(res => {
                    if (res.data.code === SUCCESS) {
                        this.orders = res.data.data;
                    }
                });
            },
            getaddress() {
                getaddress('/api/address/1').then(res => {
                    if (res.data.code === SUCCESS) {
                        this.address = res.data.data;
                    }
                })
            },
            suborder() {
                updataorder('/api/orders/'+this.orders.oid, {
                    remark:this.remark,
                    status:1
                }).then(res => {
                    if (res.data.code === SUCCESS) {
                        this.$router.push({name:'pay',query:{price:this.orders.price}});
                    }
                })
            }
        },
        beforeMount() {
            this.ordernum = this.$route.query.ordernum;
            console.log(this.ordernum);
            this.getaddress();
            this.getoederdata(this.ordernum);
        }

    }
</script>

